@import "settings";
@import "shared";

/* =============================================
  Overrides for bootstrap CSS
 @mixin ============================================*/
.navbar-inner {
  border: 0;
  padding: 0; }

/* Sections (Shared) */
section {
  &.next-episode,
  &.about,
  &.bands,
  &.anime,
  &.games,
  &.drinks,
  &.partners {
    margin-bottom: 50px;

    li {
      margin-bottom: 10px; }

    .heading {
      border-bottom: 1px solid $section-heading-color;
      color: $section-heading-color;
      margin-bottom: 25px;
      padding-bottom: 20px; }

    .sub-heading {
      text-align: center; }

    .col1 {
      margin: 0; }

    .event-details {
      .icon {
        @include icon(white, 36px);
        display: inline-block;
        margin-right: 15px;
        text-align: center;
        width: 36px; } } } }

/* =============================================
  Common stylings
 @mixin ============================================*/
/* Section: Hero Banner */
section {
  &.hero-unit {
    padding-bottom: 0; } }

/* Section: Social Media */
section {
  &.social-media {
    margin-bottom: 30px;
    text-align: right;

    .link {
      margin-right: 10px;
      text-decoration: none; }

    .icon {
      color: $social-media-icon-color;
      font-size: 40px;

      &.facebook:hover {
        color: #3f51b5; }

      &.twitter:hover {
        color: #87ceeb; }

      &.instagram:hover {
        color: #9c27b0; } } } }

/* Section: About */
section {
  &.about {
    .thumbnails-2-cols {
      margin-top: 20px;

      .thumbnail {
        border: 0; } } } }

/* Section: Bands */
section {
  &.bands {
    .band-list {
      li {
        text-align: center; }

      .band-name {
        margin-bottom: 25px; } }

    .thumbnail {
      &.round {
        border: 0;
        margin: 0 auto;
        width: 80%; } } } }

/* Section: Anime */
section {
  &.anime {
    .frm-anime-survey {
      margin-top: 25px;

      label {
        > span {
          min-height: 18px; } } } } }

/* Section: Games */
section {
  &.games {
    .frm-games-survey {
      margin-top: 25px;

      label {
        > span {
          min-height: 18px; } } } } }

/* Section: Drinks */
section {
  &.drinks {
    .specials-list {
      .list-item {
        text-align: center;

        .specials-photo {
          width: 40%; } } }

    .beer-list {
      margin-left: 0;

      .heading {
        border: 0;
        margin: 40px 0 20px 0;
        padding: 0;
        text-align: center; } }

    .draft-list,
    .bottled-list {
      margin: 0;
      overflow: hidden;

      .list-item {
        float: left;
        text-align: center;
        width: 33%;

        .draft-photo,
        .bottled-photo {
          width: 15vh; } } } } }

/* Section: Partners */
section {
  &.partners {
    .partner-list {
      .list-item {
        .partner-photo {
          width: 25vh; } } } } }

/* =============================================
  Media queries
 @mixin ============================================*/
@media (min-width: 768px) and (max-width: 979px) {
  .navbar-fixed-left {
    .container {
      width: 104px; } } }

// Tablets (vertical)
@media (max-width: 767px) {
  .navbar-fixed-left {
    margin-right: -20px;
    margin-left: -20px; } }

// Tablets (horizontal)
@media (max-width: 979px) {
  .navbar {
    .nav {
      > li {
        .icon {
          @include icon(white, 30px);
          margin-right: 10px; }

        .title {
          display: inline-block;
          font-size: 2.8vh;
          margin: 0; } } } }

  section {
    &.hero-unit {
      display: none; } }

  section {
    &.drinks {
      .draft-list,
      .bottled-list {
        .list-item {
          .draft-photo,
          .bottled-photo {
            width: 12vh; } } } } } }

// Desktop only
@media (min-width: 980px) {
  body {
    padding: 0; }

  .hero-unit {
    background-color: #000;
    display: block; }

  /* Navbar */
  .container-navbar {
    margin: 0;

    .navbar {
      height: 100vh;
      overflow: auto;
      position: fixed;

      .nav {
        margin: 0;

        > li {
          color: $navbar-item-color;
          float: none;
          margin-bottom: 15px;
          padding: 15px;
          text-align: center;

          &.active {
            background-color: #111; }

          > a {
            color: $navbar-item-color;
            padding: 0; }

          .icon {
            @include icon(white, 36px); }

          .title {
            font-size: $navbar-item-size;
            line-height: 26px;
            margin: 0;
            text-transform: uppercase; } } } } } }

@media (min-width: 1200px) {
  .navbar-fixed-left {
    .container {
      width: 170px; } } }
